.file-input
  label
    all: unset
    padding: 6px 16px
    margin: 6px
    background-color: #eee
    border-radius: 32px
    font-size: 18px
    white-space: nowrap
    cursor: pointer
    transition: background-color 50ms
    
    &::before
      content: "image"
      font-family: "Material Symbols Outlined"
      font-size: 24px
      line-height: 24px
      vertical-align: bottom
      margin-right: 8px

    &:hover
      background-color: #ddd
      

  input[type=file]
    display: none